* {
    box-sizing: border-box;
}

body {
    font-family: "Poppins", sans-serif;
    max-width: 550px;
    margin: 0 auto;
}

nav {
    height: 70px;
    display: flex;
    padding: 20px 36px;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.bold {
    font-weight: bold;
}

.gray {
    color: #918e9b;
}

.nav--logo {
    max-width: 100px;
}

section {
    padding: 20px;
}

.hero {
    display: flex;
    flex-direction: column;
}

.hero--photo {
    max-width: 400px;
    align-self: center;
}

.hero--header {
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 36px;
    line-height: 110%;
    color: #000000;
}

.hero--text {
    margin-top: 0;
    font-weight: 300;
    font-size: 16px;
    line-height: 110%;
    color: #222222;
    max-width: 320px;
}

.cards-list {
    display: flex;
    flex-wrap: nowrap;
    gap: 20px;
    overflow-x: auto;
}

.card {
    width: 175px;
    font-size: 12px;
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    position: relative;
}

.card--image {
    width: 100%;
    border-radius: 9px;
    margin-bottom: 9px;
}

.card--title {
    overflow: hidden;
    text-overflow: ellipsis;
}

.card--stats {
    display: flex;
    align-items: center;
}

.card--star {
    padding-right: 5px;
    padding-bottom: 3px;
    height: 14px;
}

.card--rating {
    font-weight: 300;
    line-height: 110%;
    color: #222222;
}

.card--submissions,
.card--location {
    font-weight: 300;
    line-height: 110%;
    color: #918e9b;
    margin: 0 3px;
}

.card--badge {
    position: absolute;
    top: 6px;
    left: 6px;
    background: white;
    padding: 1px 7px;
    border-radius: 2px;
    font-weight: bold;
    font-size: 10px;
}

.card > p {
    margin: 5px;
}

.card--price {
    margin-top: auto;
}
